<template>
  <div class="query-list-wrapper">
    <QueryListPage ref="taizhangQueryListPage"
        :query-form="queryForm"
        :query-list="queryList"></QueryListPage>
  </div>
</template>
<script>
export default {
  data () {
    return {
      queryListPageRef: 'taizhangQueryListPage',
      queryForm: {
        queryObject: {
          no: {
            type: 'text',
            label: ''
          },
          deal_no: {
            type: 'text',
            label: ''
          },
          goods_name: {
            type: 'text',
            label: ''
          },
          dateRange: {
            type: 'datetimerange',
            fieldsName: ['start_time', 'end_time'],
            label: ''
          }
        },
        beforeQuery: this.beforeQuery
      },
      queryList: {
        title: '',
        url: `${apiTaizhangList}`,
        actions: [],
        columns: [
          {title: '', key: 'no', width: 63},
          {
            title: '', 
            key: 'time',
            render: (h, params) => {
              return this._v(this.$moment(params.row.createtime).format('YYYY-MM-DD HH:mm:ss'))
            },
            width: 135
          },
          {title: '', key: 'deal_no', width: 125},
          {title: '', key: 'goods_no', width: 85},
          {title: '', key: 'goods_name', width: 85},
          {title: '', key: 'goods_guige', width: 120},
          {title: '', key: 'buy_quantity', width: 85},
          {title: '', key: 'buy_price', width: 85},
          {title: '', key: 'buy_money', width: 85},
          {title: '', key: 'sale_quantity', width: 85},
          {title: '', key: 'sale_price', width: 85},
          {title: '', key: 'sale_money', width: 85},
          {title: '', key: 'store_quantity', width: 85},
          {title: '', key: 'store_money', width: 85},
          {title: '', key: 'store_aprice', width: 85},
          {title: '', key: 'comment', width: 260}
        ]
      }
    }
  }
}
</script>




