<template>
  <div>
    <FormPage ref="goodsAddForm"
              :form-config="formConfig"></FormPage>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        formConfig: {
          formTitle: '',
          formUrl: `${apiGoodsAdd}`,
          formModel: {},
          formItems: {
            name: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            guige: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            unit: {
              type: 'select',
              label: '',
              config: {
                data: this.$SysCode.UnitType
              },
              halfColumn: true
            },
            conversion: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            quantity: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            aprice: {
              type: 'text',
              label: '',
              halfColumn: true
            }
          },
          formActions: [
            {text: '', type: 'submit'},
            {text: '', type: 'cancel'}
          ],
          formRules: {
            name: [
              {required: true, message: '', trigger: 'blur'}
            ]
          },
          beforeSubmit: this.beforeSubmit
        }
      }
    },
    methods: {
      beforeSubmit (data) {
        data.creater = this.$loginUser.id
        return data
      }
    }
  }
</script>



