<template>
  <div>
    <FormPage ref="customerEditForm"
              :form-config="formConfig"></FormPage>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        formConfig: {
          formTitle: '',
          formUrl: `${apiCustomerEdit}`,
          formModel: {},
          formItems: {
            name: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            code: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            shuiwu: {
              type: 'select',
              label: '',
              config: {
                data: this.$SysCode.ShuiwuType
              },
              halfColumn: true
            },
            person: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            phone: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            mobile: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            postcode: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            fax: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            bank: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            bank_no: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            email: {
              type: 'text',
              label: '',
              halfColumn: true
            },
            address: {
              type: 'text',
              label: ''
            },
            comment: {
              type: 'textarea',
              label: ''
            }
          },
          formActions: [
            {text: '', type: 'submit'},
            {text: '', type: 'cancel'}
          ],
          formRules: {
            name: [
              {required: true, message: '', trigger: 'blur'}
            ]
          },
          beforeSubmit: this.beforeSubmit
        }
      }
    },
    methods: {
      beforeSubmit (data) {
        // data.creater = this.$loginUser.id
        return data
      }
    },
    mounted () {
      this.formConfig.formModel.id = this.$route.query.id
      this.$http.get(`${apiCustomerById}?id=${this.$route.query.id}`)
        .then(response => {
          if (response.data.code === 200) {
            let customer = response.data.data
            this.$refs['customerEditForm'].initFormData(customer)
            this.formConfig.formTitle = ' ' + customer.no
          }
        })
    }
  }
</script>



