<template>
  <div>
    <FormPage ref="userAddForm"
              :form-config="formConfig"></FormPage>
  </div>
</template>
<script>
  import MD5 from 'crypto-js/md5'
  export default {
    data () {
      return {
        formConfig: {
          formTitle: '',
          formUrl: `${apiUserAdd}`,
          formModel: {},
          formItems: {
            name: {
              type: 'text',
              label: '',
              maxlength: 20,
              halfColumn: true
            },
            cnname: {
              type: 'text',
              label: '',
              maxlength: 20,
              halfColumn: true
            },
            password: {
              type: 'text',
              label: '',
              maxlength: 20,
              config: {
                isPassword: true
              },
              halfColumn: true
            },
            confirmPwd: {
              type: 'text',
              label: '',
              config: {
                isPassword: true
              },
              halfColumn: true
            }
          },
          formActions: [
            {text: '', type: 'submit'},
            {text: '', type: 'cancel'}
          ],
          formRules: {
            name: [
              {required: true, message: '', trigger: 'blur'},
              {remote: `${apiIsAvailabeUserName}`, message: '', trigger: 'blur'}
            ],
            cnname: [
              {required: true, message: '', trigger: 'blur'}
            ],
            password: [
              {required: true, message: '', trigger: 'blur'},
              {type: 'string', min: 6, max: 20, message: '620', trigger: 'blur'}
            ],
            confirmPwd: [
              {required: true, message: '', trigger: 'blur'},
              {validator: this.checkConfirmPwd}
            ]
          },
          beforeSubmit: this.beforeSubmit
        }
      }
    },
    methods: {
      beforeSubmit (data) {
        data.password = MD5(data.password).toString()
        return data
      },
      checkConfirmPwd (rule, value, callback, source, options) {
        if (value === this.formConfig.formModel.password) {
          callback()
        } else {
          callback(new Error(''))
        }
      }
    }
  }
</script>



